.slide {
  width: 100%;
  height: 100%;
  position: absolute;
  opacity: 0;
  transition: opacity ease-in-out 0.4s;
}

.slide img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.active-anim {
  opacity: 1;
}

.prev {
  top: 50%;
  left: 4rem;
  transform: translateY(-60%);
}

.next {
  top: 50%;
  right: 4rem;
  transform: translateY(-60%);
}

.dot {
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  margin: 0 5px;
  background: #ffffff;
  transition: 0.5s;
  box-shadow: 0 10px 20px rgba(1, 2, 0, 0.2);
  background-color: #FFFFFF;
}

.dot.active {
  width: 3.2rem;
  height: 1rem;
  border-radius: 0.7rem;
}

@media screen and (max-width: 480px) {
  
  .prev {
    top: 90%;
    left: 32.5rem;
  }
  .next {
    top: 90%;
    right: 2rem;
  }
}